<script lang="babel">
export default {
	data(){//数据
		return{
			classification:[]
		}	
	},
	methods:{//js
		cationfixed(){
			$(".cationfixedbu").css({display:"block"})
			$(".cationfixeddiv").fadeIn(200);
		},
		cationfixednone(){
			$(".cationfixedbu").css({display:"none"})
			$(".cationfixeddiv").fadeOut(200);
		}
	},
	components:{//注册模块
	},
	mounted(){//得到数据的ajax
			var _this=this;
			_this.$http({
				method: 'get', //ajax类型 方法
				url: "./static/json/ification.json", //请求地址(接口)
				params: {} //data发送后台 参数
			}).then(function(data) {
				//然后data后台响应的数据;success进入 //赋值给sliderArr数组,sliderArr滑动数组
				_this.classification = data.data; //同步值；或_this.xxxArr=data.body;
			}, function(error) {
				//错误的时候
				alert("数据请求失败请稍后再试");
			})
	}
}
</script>

