<template>
  <div @touchmove="refres">
  	<div>
  		<div v-for="obj in Refreshing">
  			<img :src="obj.imgUrl"/>
  		</div>
  		<div class="botop" @touchstart="refrestop"></div>
  	</div>
  </div>
</template>

<script lang="babel">
export default {
	data(){//数据
		return{
			Refreshing:[]
		}	
	},
	methods:{//js
		cationfixed(){
			$(".cationfixedbu").css({display:"block"})
			$(".cationfixeddiv").fadeIn(200);
		},
		cationfixednone(){
			$(".cationfixedbu").css({display:"none"})
			$(".cationfixeddiv").fadeOut(200);
		},
		refres() {//向上按键淡入淡出
				var Height=$(document).height();
				var Top=$(window).scrollTop();
				if(Top>Height*0.7){
					$(".botop").fadeIn(500);
				}
				else{
					$(".botop").fadeOut(500);
				}
        	},
        	refrestop(){
        		$("html,body").animate({scrollTop:0},400);
        	}
	},
	components:{//注册模块
	},
	mounted(){//得到数据的ajax
			var _this=this;
			_this.$http({
				method: 'get', //ajax类型 方法
				url: "./static/json/Refreshing.json", //请求地址(接口)
				params: {} //data发送后台 参数
			}).then(function(data) {
				//然后data后台响应的数据;success进入 //赋值给sliderArr数组,sliderArr滑动数组
				_this.Refreshing = data.data; //同步值；或_this.xxxArr=data.body;
			}, function(error) {
				//错误的时候
				alert("数据请求失败请稍后再试");
			})
	}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang='scss' >
		.botop{
		text-align: center;
    color: #fff;
    width: 55px;
    height: 55px;
    line-height: 55px;
    background: url('../assets/images/backtop.png') no-repeat;
    background-size: 100%;
    position: fixed;
    right: 6px;
    bottom: 0px;
    display: none;
    z-index: 2;}
</style>


