<template>
	<div class="head">
		<div class="menu" @touchstart="appfun" @cilck="appfun">
			<div class="fa fa-bars "></div>
		</div>
		<div class="search">
			<div class="glass"><i class="fa fa-search"></i></div>
			<div class="input" @touchstart="according">搜索</div>
		</div>
		<div class="gender">
			<div class="neuter">
				<i class="fa fa-neuter"></i>
			</div>
			<div class="stroke">
				<i class="fa fa-mars-stroke"></i>
			</div>
		</div>
		<div class="position" style="display: none;">
			<div class="position-head">
				<div class="angle" @touchstart="hidden">
					<i class="fa fa-angle-left"></i>
				</div>
				<div class="position-search">
					<i class="fa fa fa-search"></i>
				</div>
				<input type="text" value="" class="search-text" />
				<input type="button" value="搜索" class="search-button" @touchstart="history" />
			</div>
			<div class="Recently">
				<div class="Recently-div clearfix">
					<span>最近搜索</span>
					<i class="fa fa-trash-o" @touchstart="empty"></i>
				</div>
				<ul class="Recently-ul clearfix">
					<li class="Recently-li">暂无历史</li>
				</ul>
			</div>
			<div class="hot">
				<div class="hot-div clearfix">
					<span class="hot-left">热门搜索</span>
					<span class="hot-right" @touchstart="infun">换一换</span>
				</div>
				<ul class="hot-ul clearfix">
					<li v-for="obj in hotArr" :to="obj.href">{{obj.items}}</li>
				</ul>
			</div>
		</div>
		<div class="cloth" v-if="showbool" @touchstart="rightfun">
		</div>

	</div>
</template>

<script lang="babel">
	export default {
		data() { //数据
			return {
				showbool: false,
				hotArr: []

			}
		},
		methods: { //js
			appfun() { //整个app向右移动
				var app = $(".v-hidde");
				if(app.is(".slatefo")) {
					app.removeClass("slatefo");
					$(".height").css({ overflow: "hidden" })
				}
				app.addClass("slate");
				$(".app").css({"overflow":"hidden"});
				this.showbool = true;
			},
			rightfun() { //整个app向右移动
				var app = $(".v-hidde");
				if(app.is(".slate")) {
					app.removeClass("slate");
					$(".height").removeAttr("style");
				}
				app.addClass("slatefo");
				$(".app").removeAttr("style");
				this.showbool = false;
			},
			according() { //显示
				$(".position").css({ "display": "" });
				$(".app").css({"overflow":"hidden"});
			},
			hidden() { //隐藏
				$(".position").css({ "display": "none" });
				$(".app").removeAttr("style");
			},
			infun() {

			},
			empty() { //清空
				$(".Recently-ul").empty();
				var li = document.createElement("li");
				$(".Recently-ul").append(li);
				$(".Recently-ul li:last").text("暂无历史");
				$(".Recently-ul li:last").addClass("Recently-li");
				alert("清空成功");
			},
			history() {
				var searchText = $(".search-text");
				var value = searchText.val();
				if(value != "") {
					$(".Recently-li").remove();
					var li = document.createElement("li");
					$(".Recently-ul").append(li);
					$(".Recently-ul li:last").text(value);
				} else {
					alert("请输入物品")
				}
			}

		},
		components: { //注册模块
		},
		mounted() { //得到数据的ajax
			var _this = this;
			_this.$http({
				method: 'get', //ajax类型 方法
				url: "./static/json/hot.json", //请求地址(接口)
				params: {} //data发送后台 参数
			}).then(function(data) {
				//然后data后台响应的数据;success进入 //赋值给sliderArr数组,sliderArr滑动数组
				_this.hotArr = data.data; //同步值；或_this.xxxArr=data.body;
			}, function(error) {
				//错误的时候
				alert("数据请求失败请稍后再试");
			})

		}
	}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang='scss'>
	.cloth {
		width: 100%;
		height: 100%;
		position: absolute;
		z-index: 99;
		left: 0px;
		top: 0px;
	}
	
	.head {
		width: 100%;
		height: 44px;
		background: #f3f3f3;
		display: flex;
	}
	
	.menu {
		width: 10%;
		height: 44px;
		text-align: center;
		line-height: 44px;
	}
	
	.input {
		width: 70%;
		height: 31px;
		border-radius: 0px 15px 15px 0px;
		border: none;
		background: #fff;
		color: #D8D8D8;
		font-size: 1em;
		line-height: 31px;
	}
	
	.search {
		width: 70%;
		height: 31px;
		margin-top: 7px;
		display: flex;
	}
	
	.glass {
		width: 20%;
		height: 31px;
		text-align: center;
		line-height: 31px;
		background: #fff;
		border-radius: 15px 0px 0px 15px;
	}
	
	.fa-search {
		color: #e3e3e3;
	}
	
	.gender {
		margin-top: 7px;
		display: flex;
	}
	
	.neuter,
	.stroke {
		width: 28px;
		height: 28px;
		text-align: center;
		line-height: 28px;
		color: #fff;
	}
	
	.neuter {
		background: #ccc;
	}
	
	.stroke {
		background: #d63838;
		margin-left: 3px;
	}
	
	.position {
		position: absolute;
		height: 680px;
		width: 100%;
		box-sizing: border-box;
		display: flex;
		z-index: 1;
		background: #fff;
		flex-direction: column;
		left: 0;
		z-index: 4;
	}
	
	.position-head {
		border-bottom: 1px solid #ddd;
		display: flex;
		height: 29px;
		padding: 10px 0px 9px 7px;
	}
	
	.position-search {
		width: 30px;
		height: 29px;
		text-align: center;
		line-height: 29px;
		background: #f7f7f7;
		border-radius: 10px 0px 0px 10px;
	}
	
	.search-text {
		width: 290px;
		height: 29px;
		border: none;
		border-radius: 0px 10px 10px 0px;
		background: #f7f7f7;
	}
	
	.search-button {
		margin-left: 10px;
		width: 46px;
		height: 29px;
		border-radius: 10px;
		background: #007aff;
		border: none;
		color: #fff;
		text-align: center;
		line-height: 29px;
	}
	
	.Recently {
		width: 100%;
		box-sizing: border-box;
		border-bottom: 1px solid #ddd;
		padding: 13px 0px 0px 0px;
		flex-direction: column;
	}
	
	.Recently-div {
		margin: 0px 13px 0px 7px;
		height: 17px;
		& span {
			line-height: 17px;
			font-size: 13px;
			float: left;
			display: block;
		}
		& i {
			width: 14px;
			height: 17px;
			text-align: center;
			line-height: 17px;
			display: block;
			float: right;
			color: #8f8f94;
		}
	}
	
	.hot {
		margin-top: 13px;
		display: flex;
		flex-direction: column;
	}
	
	.hot-div {
		height: 20px;
		padding: 0px 10px 0px 7px;
	}
	
	.hot-left {
		display: block;
		float: left;
		line-height: 20px;
		font-size: 14px;
	}
	
	.hot-right {
		display: block;
		float: right;
		line-height: 20px;
		font-size: 14px;
		color: #4eb6be;
	}
	
	.hot-ul,
	.Recently-ul {
		padding: 17px 41px 0px 8px;
		box-sizing: border-box;
		& li {
			margin: 0px 6px 12px 0px;
			padding: 6px 8px 6px 8px;
			border-radius: 6px;
			border: 1px solid #dddddd;
			color: #9fa09f;
			float: left;
			font-size: 12px;
			text-align: center;
			line-height: 11px;
			background: #f7f7f7;
		}
	}
	
	.angle {
		width: 21px;
		height: 29px;
		text-align: center;
		line-height: 29px;
	}
</style>


