<script lang="babel">
	import headA from "./common/head";
	import tail from "./common/tail";
	export default {
		data() { //数据
			return {
				start: 0, //起始坐标
				end: 0, //结束坐标
				moveX: 0, //表示移动距离
				endX: 0, //当前位置
				showHideBool: false, //显示fix层的boolean值
				loadHideBool: false, //加载时让fix不做动画
				sliderAniBool: false, //是否让幻灯片做动画
				slide: [],
				cakes: [],
				popular: [],
				coat:[],
				coat2:[],
				coat3:[],
				coat4:[],
				classification:[]
			}
		},
		methods: { //js
			jsScroll(e) {
				console.log(this.classification)
				var toucher = e.changedTouches[0]; //得到一个触点
				var _this = this;
				var switchliAll = $('.slide-ul li');
				var Width = switchliAll.width(); //可视宽度				
				if(e.type != "touchend") {
					var target = e.targetTouches[0];
					var pageX = target.pageX;
					var pageY = target.pageY;
				}
				if(e.type == "touchstart") {
					_this.start = toucher.pageX;
					_this.sliderAniBool = false;
				} else if(e.type == "touchmove") {
					_this.end = toucher.pageX;
					_this.moveX = -Width * _this.endX + _this.end - _this.start;
					if(Math.abs(_this.end - _this.start) > 0) {
						e.preventDefault(); //消除默认效果
					}
				} else if(e.type == "touchend") {
					var width5 = Width / 5;
					if(_this.end - _this.start > width5) { //手指向右

						if(_this.endX != 0) {

							_this.endX--;
						}
					} else if(_this.end - _this.start < -width5) { //手指向左
						if(_this.endX != _this.slide.length - 1) {

							_this.endX++;

						}
					}
					_this.moveX = -_this.endX * Width;
					_this.sliderAniBool = true;
					//					console.log(_this.moveX,-_this.endX)

				}
			},
			scrollbar() {//向上按键淡入淡出
				var Height=$(document).height();
				var Top=$(window).scrollTop();
				if(Top>Height*0.7){
					$(".totop").fadeIn(500);
				}
				else{
					$(".totop").fadeOut(500);
				}
        	},
        	jstotop(){
        		$("html,body").animate({scrollTop:0},400);
        	}

		},
		components: { //注册模块
			headA,
			tail
		},
		mounted() { //得到数据的ajax	
			var _this = this;
			
			_this.$http({
				method: 'get', //ajax类型 方法
				url: "./static/json/slide.json", //请求地址(接口)
				params: {} //data发送后台 参数
			}).then(function(data) {
				//然后data后台响应的数据;success进入 //赋值给sliderArr数组,sliderArr滑动数组
				_this.slide = data.data; //同步值；或_this.xxxArr=data.body;
			}, function(error) {
				//错误的时候
				alert("数据请求失败请稍后再试");
			})
			_this.$http({
